import { supabase, getTenantId } from './supabase'
import PerfilUsuarioService from './perfilUsuarioService'
import EmailNotificationService from './emailNotificationService'

/**
 * Servio para gerenciamento de tramitao de processos administrativos
 */
export class TramitacaoProcessosService {
  
  // =====================================================
  // FLUXOS DE STATUS POR TIPO DE PROCESSO
  // =====================================================
  
  static FLUXO_PADRONIZACAO = [
    'rascunho',
    'aguardando_aprovacao',  // Status inicial quando processo  finalizado no assistente
    'criado_cpm',
    'aprovado_cpm', 
    'assinado_admin',
    'julgamento_ccl',
    'aprovado_ccl',
    'aprovado_juridico',
    'edital_publicado',
    'homologado',
    'finalizado'
  ]
  
  static FLUXO_DESPADRONIZACAO = [
    'rascunho',
    'aguardando_aprovacao',  // Status inicial quando processo  finalizado no assistente
    'em_criacao_desp',
    'criado_cmp_desp',
    'submetido_autoridade_desp',
    'abertura_autorizada_desp',
    'aviso_publicado',
    'com_recurso_desp',
    'homologado_desp',
    'excluindo_marcas'
  ]

  // =====================================================
  // MAPEAMENTO STATUS -> PERFIL RESPONSVEL
  // =====================================================
  
  static RESPONSAVEL_POR_STATUS = {
    // PADRONIZAO
    'rascunho': PerfilUsuarioService.PERFIS.CPM,
    'aguardando_aprovacao': PerfilUsuarioService.PERFIS.CPM, // CPM deve aprovar o processo
    'criado_cpm': PerfilUsuarioService.PERFIS.CPM,
    'aprovado_cpm': PerfilUsuarioService.PERFIS.ORGAO_ADMINISTRATIVO,
    'assinado_admin': PerfilUsuarioService.PERFIS.CCL,
    'julgamento_ccl': PerfilUsuarioService.PERFIS.CCL,
    'aprovado_ccl': PerfilUsuarioService.PERFIS.ASSESSORIA_JURIDICA,
    'aprovado_juridico': PerfilUsuarioService.PERFIS.ORGAO_ADMINISTRATIVO,
    'edital_publicado': PerfilUsuarioService.PERFIS.ORGAO_ADMINISTRATIVO,
    'homologado': PerfilUsuarioService.PERFIS.ORGAO_ADMINISTRATIVO,
    
    // DESPADRONIZAO
    'em_criacao_desp': PerfilUsuarioService.PERFIS.CPM,
    'criado_cpm_desp': PerfilUsuarioService.PERFIS.CPM,
    'submetido_autoridade_desp': PerfilUsuarioService.PERFIS.ORGAO_ADMINISTRATIVO,
    'abertura_autorizada_desp': PerfilUsuarioService.PERFIS.CCL,
    'aviso_publicado': PerfilUsuarioService.PERFIS.CCL,
    'com_recurso_desp': PerfilUsuarioService.PERFIS.ASSESSORIA_JURIDICA,
    'homologado_desp': PerfilUsuarioService.PERFIS.ORGAO_ADMINISTRATIVO
  }

  // =====================================================
  // TRAMITAO DE PROCESSOS
  // =====================================================

  /**
   * Enviar processo para prxima etapa
   */
  static async enviarProcesso(processoId, observacoes = '') {
    try {
      console.log(' Enviando processo para prxima etapa:', processoId)
      
      // Obter dados do processo
      const { data: processo, error: errorProcesso } = await supabase
        .from('processos_administrativos')
        .select('*')
        .eq('id', processoId)
        .single()
      
      if (errorProcesso) throw errorProcesso
      
      // Log para debug
      console.log(' DEBUG TRAMITAO:')
      console.log('Status atual:', processo.status)
      console.log('Tipo processo:', processo.tipo_processo)
      
      // Determinar prximo status
      const proximoStatus = this.obterProximoStatus(processo.status, processo.tipo_processo)
      console.log('Prximo status calculado:', proximoStatus)
      
      if (!proximoStatus) {
        console.error(' Status atual no encontrado nos fluxos!')
        console.error('Status atual:', processo.status)
        console.error('Tipo processo:', processo.tipo_processo)
        console.error('Fluxo esperado:', processo.tipo_processo === 'padronizacao' ? this.FLUXO_PADRONIZACAO : this.FLUXO_DESPADRONIZACAO)
        throw new Error(`No h prxima etapa disponvel para este processo. Status atual: ${processo.status}`)
      }
      
      // Atualizar status do processo
      const { error: errorUpdate } = await supabase
        .from('processos_administrativos')
        .update({
          status: proximoStatus,
          data_ultima_tramitacao: new Date().toISOString()
        })
        .eq('id', processoId)
      
      if (errorUpdate) throw errorUpdate
      
      // Registrar histrico de tramitao
      await this.registrarTramitacao(processoId, processo.status, proximoStatus, 'ENVIADO', observacoes)
      
      // Enviar notificao por email
      await EmailNotificationService.enviarNotificacaoTramitacao(processoId, processo.status, proximoStatus, observacoes)
      
      console.log(` Processo ${processoId} enviado: ${processo.status}  ${proximoStatus}`)
      
      return {
        sucesso: true,
        statusAnterior: processo.status,
        statusNovo: proximoStatus,
        proximoResponsavel: this.RESPONSAVEL_POR_STATUS[proximoStatus]
      }
      
    } catch (error) {
      console.error(' Erro ao enviar processo:', error)
      throw error
    }
  }

  /**
   * Devolver processo para etapa anterior
   */
  static async devolverProcesso(processoId, motivo, observacoes = '') {
    try {
      console.log(' Devolvendo processo:', processoId)
      
      // Obter dados do processo
      const { data: processo, error: errorProcesso } = await supabase
        .from('processos_administrativos')
        .select('*')
        .eq('id', processoId)
        .single()
      
      if (errorProcesso) throw errorProcesso
      
      // Determinar status de devoluo (geralmente volta para CPM)
      const statusDevolucao = processo.tipo_processo === 'padronizacao' ? 'criado_cpm' : 'criado_cpm_desp'
      
      // Atualizar status do processo
      const { error: errorUpdate } = await supabase
        .from('processos_administrativos')
        .update({
          status: statusDevolucao,
          data_ultima_tramitacao: new Date().toISOString()
        })
        .eq('id', processoId)
      
      if (errorUpdate) throw errorUpdate
      
      // Registrar histrico de tramitao
      await this.registrarTramitacao(processoId, processo.status, statusDevolucao, 'DEVOLVIDO', `${motivo}. ${observacoes}`)
      
      // Enviar notificao por email
      await EmailNotificationService.enviarNotificacaoTramitacao(processoId, processo.status, statusDevolucao, `${motivo}. ${observacoes}`)
      
      console.log(` Processo ${processoId} devolvido: ${processo.status}  ${statusDevolucao}`)
      
      return {
        sucesso: true,
        statusAnterior: processo.status,
        statusNovo: statusDevolucao,
        motivo: motivo
      }
      
    } catch (error) {
      console.error(' Erro ao devolver processo:', error)
      throw error
    }
  }

  /**
   * Rejeitar processo
   */
  static async rejeitarProcesso(processoId, motivo, observacoes = '') {
    try {
      console.log(' Rejeitando processo:', processoId)
      
      // Obter dados do processo
      const { data: processo, error: errorProcesso } = await supabase
        .from('processos_administrativos')
        .select('*')
        .eq('id', processoId)
        .single()
      
      if (errorProcesso) throw errorProcesso
      
      // Determinar perfil atual para definir tipo de rejeio
      const perfilAtual = await PerfilUsuarioService.obterPerfilUsuarioAtual()
      let statusRejeicao
      
      switch (perfilAtual.perfil_usuario) {
        case PerfilUsuarioService.PERFIS.CPM:
          statusRejeicao = 'rejeitado_cpm'
          break
        case PerfilUsuarioService.PERFIS.ORGAO_ADMINISTRATIVO:
          statusRejeicao = 'rejeitado_admin'
          break
        case PerfilUsuarioService.PERFIS.CCL:
          statusRejeicao = 'rejeitado_ccl'
          break
        case PerfilUsuarioService.PERFIS.ASSESSORIA_JURIDICA:
          statusRejeicao = 'rejeitado_juridico'
          break
        default:
          statusRejeicao = 'rejeitado_final'
      }
      
      // Atualizar status do processo
      const { error: errorUpdate } = await supabase
        .from('processos_administrativos')
        .update({
          status: statusRejeicao,
          data_ultima_tramitacao: new Date().toISOString()
        })
        .eq('id', processoId)
      
      if (errorUpdate) throw errorUpdate
      
      // Registrar histrico de tramitao
      await this.registrarTramitacao(processoId, processo.status, statusRejeicao, 'REJEITADO', `${motivo}. ${observacoes}`)
      
      // Enviar notificao por email
      await EmailNotificationService.enviarNotificacaoTramitacao(processoId, processo.status, statusRejeicao, `${motivo}. ${observacoes}`)
      
      console.log(` Processo ${processoId} rejeitado: ${processo.status}  ${statusRejeicao}`)
      
      return {
        sucesso: true,
        statusAnterior: processo.status,
        statusNovo: statusRejeicao,
        motivo: motivo
      }
      
    } catch (error) {
      console.error(' Erro ao rejeitar processo:', error)
      throw error
    }
  }

  // =====================================================
  // UTILITRIOS
  // =====================================================

  /**
   * Obter prximo status na sequncia
   */
  static obterProximoStatus(statusAtual, tipoProcesso) {
    const fluxo = tipoProcesso === 'padronizacao' ? this.FLUXO_PADRONIZACAO : this.FLUXO_DESPADRONIZACAO
    const indiceAtual = fluxo.indexOf(statusAtual)
    
    if (indiceAtual === -1 || indiceAtual === fluxo.length - 1) {
      return null // No h prximo status
    }
    
    return fluxo[indiceAtual + 1]
  }

  /**
   * Verificar se usurio pode tramitar processo
   */
  static async podeUsuarioTramitar(processo) {
    try {
      const perfilUsuario = await PerfilUsuarioService.obterPerfilUsuarioAtual()
      const perfilResponsavel = this.RESPONSAVEL_POR_STATUS[processo.status]
      
      return perfilUsuario.perfil_usuario === perfilResponsavel
    } catch (error) {
      console.error('Erro ao verificar permisso de tramitao:', error)
      return false
    }
  }

  /**
   * Obter aes disponveis para um processo
   */
  static async obterAcoesDisponiveis(processo) {
    const podeTrampitar = await this.podeUsuarioTramitar(processo)
    
    if (!podeTrampitar) {
      return []
    }
    
    const acoes = []
    const proximoStatus = this.obterProximoStatus(processo.status, processo.tipo_processo)
    
    // Ao de enviar para prxima etapa
    if (proximoStatus) {
      acoes.push({
        tipo: 'ENVIAR',
        label: ' Enviar para Anlise',
        descricao: `Enviar para ${this.obterNomeProximaEtapa(proximoStatus)}`,
        cor: 'success'
      })
    }
    
    // Ao de devolver (exceto CPM)
    const perfilUsuario = await PerfilUsuarioService.obterPerfilUsuarioAtual()
    if (perfilUsuario.perfil_usuario !== PerfilUsuarioService.PERFIS.CPM) {
      acoes.push({
        tipo: 'DEVOLVER',
        label: ' Devolver para CPM',
        descricao: 'Devolver processo para correes',
        cor: 'warning'
      })
    }
    
    // Ao de rejeitar
    acoes.push({
      tipo: 'REJEITAR',
      label: ' Rejeitar Processo',
      descricao: 'Rejeitar processo definitivamente',
      cor: 'danger'
    })
    
    return acoes
  }

  /**
   * Obter nome da prxima etapa
   */
  static obterNomeProximaEtapa(status) {
    const nomes = {
      'aprovado_cpm': 'rgo Administrativo',
      'assinado_admin': 'CCL - Comisso de Licitao',
      'julgamento_ccl': 'CCL - Julgamento',
      'aprovado_ccl': 'Assessoria Jurdica',
      'aprovado_juridico': 'rgo Administrativo',
      'edital_publicado': 'Publicao de Edital',
      'homologado': 'Homologao',
      'submetido_autoridade_desp': 'rgo Administrativo',
      'abertura_autorizada_desp': 'CCL - Anlise',
      'aviso_publicado': 'Publicao de Aviso',
      'com_recurso_desp': 'Assessoria Jurdica',
      'homologado_desp': 'Homologao Final'
    }
    
    return nomes[status] || 'Prxima Etapa'
  }

  /**
   * Registrar histrico de tramitao
   */
  static async registrarTramitacao(processoId, statusAnterior, statusNovo, acao, observacoes = '') {
    try {
      const { data: sessionData } = await supabase.auth.getSession()
      const user = sessionData?.session?.user
      const tenantId = await getTenantId()
      
      const { error } = await supabase
        .from('historico_tramitacao')
        .insert([{
          processo_id: processoId,
          tenant_id: tenantId,
          status_anterior: statusAnterior,
          status_novo: statusNovo,
          acao: acao,
          observacoes: observacoes,
          usuario_id: user?.id,
          data_tramitacao: new Date().toISOString()
        }])
      
      if (error) {
        console.warn('Erro ao registrar histrico (tabela pode no existir):', error)
        // No bloqueia a tramitao se no conseguir registrar histrico
      }
    } catch (error) {
      console.warn('Erro ao registrar histrico:', error)
    }
  }

}

export default TramitacaoProcessosService